import react from "react"
import {
  MDBCard, MDBCardBody, MDBCardText, MDBFooter, MDBRow
} from "mdbreact";
import FadeIn from "react-fade-in";
import projectlist from "../images/project.json";


class Projects extends react.Component{
    constructor(props){
        super(props)
    }
    render(){
        const data2021=projectlist.projects2021;
        const project2021list= data2021.map((project)=>{
                return (
                  <MDBCard
                    style={{
                      backgroundColor: this.props.darkmode
                        ? "#7d7d7d"
                        : "#ededed",
                      marginTop: "25px",
                    }}
                  >
                    <FadeIn>
                      <MDBRow>
                        <MDBCard
                          style={{
                            height: "15rem",
                            textAlign: "center",
                            backgroundColor: this.props.darkmode
                              ? "#a1a1a1"
                              : "#ededed",
                            padding: "10px",
                            width: "27rem",
                          }}
                        >
                          <MDBCardBody>
                            <MDBCardText
                              style={{
                                color: this.props.darkmode ? "white" : null,
                              }}
                            >
                              <a style={{ fontSize: "20px" }}>{project.name}</a>
                              <hr></hr>
                              {project.description}
                            </MDBCardText>
                          </MDBCardBody>
                          <MDBFooter
                            style={{
                              color: this.props.darkmode ? "white" : "grey",
                              padding: "15px",
                            }}
                          >
                            {project.technologies}
                          </MDBFooter>
                        </MDBCard>
                        <img
                          src={project.imgurl1}
                          style={{
                            float: "right",
                            padding: "10px",
                            width: "28rem",
                            height: "15rem",
                          }}
                        ></img>

                        <img
                          src={project.imgurl2}
                          style={{
                            float: "right",
                            padding: "10px",
                            width: "28rem",
                            height: "15rem",
                          }}
                        ></img>

                        <img
                          src={project.imgurl3}
                          style={{
                            float: "right",
                            padding: "10px",
                            width: "28rem",
                            height: "15rem",
                          }}
                        ></img>
                      </MDBRow>
                    </FadeIn>
                  </MDBCard>
                );
             })
                const data2020=projectlist.project2020;
                const project2020list=data2020.map((project)=>{
                return (
                  <MDBCard
                    style={{
                      backgroundColor: this.props.darkmode
                        ? "#7d7d7d"
                        : "#ededed",
                      padding: "15px",
                      marginTop: "25px",
                    }}
                  >
                    <FadeIn>
                      <MDBRow>
                        <MDBCard
                          style={{
                            height: "15rem",
                            textAlign: "center",
                            backgroundColor: this.props.darkmode
                              ? "#a1a1a1"
                              : "#ededed",
                            padding: "10px",

                            width: "26rem",
                          }}
                        >
                          <MDBCardBody>
                            <MDBCardText
                              style={{
                                color: this.props.darkmode ? "white" : null,
                              }}
                            >
                              <a style={{ fontSize: "20px" }}>{project.name}</a>
                              <hr></hr>
                              {project.description}
                            </MDBCardText>
                          </MDBCardBody>
                          <MDBFooter
                            style={{
                              color: this.props.darkmode ? "white" : "grey",
                              padding: "15px",
                            }}
                          >
                            {project.technologies}
                          </MDBFooter>
                        </MDBCard>
                        <img
                          src={project.imgurl1}
                          style={{
                            float: "right",
                            padding: "10px",
                            width: "28rem",
                            height: "15rem",
                          }}
                        ></img>

                        <img
                          src={project.imgurl2}
                          style={{
                            float: "right",
                            padding: "10px",
                            width: "28rem",
                            height: "15rem",
                          }}
                        ></img>

                        <img
                          src={project.imgurl3}
                          style={{
                            float: "right",
                            padding: "10px",
                            width: "28rem",
                            height: "15rem",
                          }}
                        ></img>
                      </MDBRow>
                    </FadeIn>
                  </MDBCard>
                );
        })

        return (
          <div style={{ paddingTop: "30px", color:this.props.darkmode?"white":null }}>
            <h2> &nbsp;&nbsp;2021</h2>
            <hr></hr>
            <div>
                <div>
                     {project2021list}
                </div>
              &nbsp;
            </div>
            <h2> &nbsp;&nbsp;2020</h2>
            <hr></hr>
            <div>
                {project2020list}
            </div>
          </div>
        );
    }

}


export default Projects;